import React, {Component} from 'react';
import QuestionComponent from "../Question/QuestionComponent";
import {questions} from "../../data/questions";

// import './FormComponent.css';

class FormComponent extends Component {
  
  constructor(props) {
    super(props);
    this.state = {
      questionsList: questions,
      groupedQuestionsList: []
    };
  }
  
  componentDidMount() {
    let groupedCategory = [...this.state.questionsList];
    groupedCategory = groupedCategory.reduce((acc, current) => {
      acc[current.category] = acc[current.category] || [];
      acc[current.category].push(current);
      return acc;
    }, {});
    this.setState({groupedQuestionsList: groupedCategory});
  }
  
  renderQuestionCategories() {
    let groupedQuestionList = {...this.state.groupedQuestionsList};
    return (
        Object.keys(groupedQuestionList).map((key) => (<div key={key}>
          {key}
          <QuestionComponent questions={groupedQuestionList[key]}/>
        </div>))
    );
  }
  
  render() {
    return (
        <div className="home-page">
          {this.renderQuestionCategories()}
        </div>
    );
  }
}

export default FormComponent;
